import React, { FC, useState, useEffect } from 'react';
import { CommonProps } from '@interface';
import './index.less';

const AdIcon: FC<CommonProps> = (props = {}) => {
    const {
        schema: {
            className = '',
            posType = 'outside', // outside | inside
            logInfo: { itemClickType = '' } = {},
        },
        style = {},
        info: { adMarkIcon, adGrayMarkIcon, adSourceDescription },
    } = props;

    const isOutPos = posType === 'outside';
    const iconClass = isOutPos ? 'icon-white' : 'icon-gray';
    const [iconUrl, setIconUrl] = useState('');
    useEffect(() => {
        // 素材外的logo都是白色logo，素材里的logo灰色色logo
        setIconUrl(isOutPos ? adMarkIcon : adGrayMarkIcon);
    }, []);

    return (
        <div
            className={`ad-union-icon ${className} ${posType} ${
                posType === 'outside' ? 'des-white' : 'des-gray'
            }`}
            data-itemClickType={itemClickType}
            style={style}
        >
            {iconUrl ? (
                <div
                    style={{ backgroundImage: `url(${iconUrl})` }}
                    className={`icon ${iconClass}`}
                />
            ) : null}
            {adSourceDescription && (
                <div className="des"> 
                    {' '}
                    {adSourceDescription}
                </div>
            )}
        </div>
    );
};

export default AdIcon;
